import { Button, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Form } from "@unform/web";
import { ClienteService, IInfoClient } from "../../services";
import { VTextField } from "../forms-components/VTextField";
import "./styles.css";
import * as Yup from "yup";
import { useVForm } from "../forms-components/UseVForm";
import { Gender, VSelectField } from "../forms-components";
import { useState } from "react";

export interface ICadastroInfo {
  cpf: number
  name: string
  rg: number
  sex: string

  address: string
  cep: number
  city: string
  uf: string
  neighborhood: string
  number: number

  cell: number
  email: string
  telephone?: number
}

export const cadastroSchema: Yup.SchemaOf<ICadastroInfo> = Yup.object().shape({
  name: Yup.string().required("O nome  obrigatro"),
  cpf: Yup.number().required("O cpf  obrigatrio").typeError("Digite apenas nmeros"),
  sex: Yup.string().required("O campo  obrigatrio"),
  rg: Yup.number().required("O RG  obrigatrio").typeError("Digite apenas nmeros"),

  address: Yup.string().required("Endereo  obrigatrio"),
  cep: Yup.number().required("CEP  obrigatrio").typeError("Digite apenas nmeros"),
  city: Yup.string().min(3, "No minimo 3 caracteres").required("Cidade  obrigatrio"),
  neighborhood: Yup.string().required("Bairro  obrigatrio"),
  number: Yup.number().required("Nmero  obrigatrio").typeError("Digite apenas nmeros"),
  uf: Yup.string().required("Estado  obrgatrio"),

  email: Yup.string().email("Digite um email vlido").required("Email  obrigatrio"),
  cell: Yup.number().required("Celular  obrigatrio").typeError("Digite apenas nmeros"),
  telephone: Yup.number().typeError("Digite apenas nmeros"),
})

export const CadastroClienteForm: React.FC<{
  update: () => void,
  handleModal: () => void
  client?: IInfoClient
  type: string
}> = ({ update, handleModal, client, type }) => {
  const close = () => { handleModal()}

  const { formRef } = useVForm()

  const handleSave = (dados: ICadastroInfo) => {
    cadastroSchema
      .validate(dados, { abortEarly: false })
      .then((dadosValidados) => {
        ClienteService.Create(dadosValidados).then((result) => {
          alert("Cliente cadastrado com sucesso!!!");
          close();
          update();
        });
      })
      .catch((erros: Yup.ValidationError) => {
        const validandoErros: { [key: string]: string } = {};
        erros.inner.forEach((erros) => {
          if (!erros.path) return;
          validandoErros[erros.path] = erros.message;
        });
        formRef.current?.setErrors(validandoErros);
      });
  }

  const handleEdit = (dados: IInfoClient) =>{
    cadastroSchema.validate(dados,{abortEarly:false})
    .then((dadosValidados)=>{
      if(dados.id)
      ClienteService.UpdateById(dados.id, dados).then(result => {
      alert("Cliente editado com sucesso!!!")
      update()
      })
    })
    .catch((erros: Yup.ValidationError)=>{
      const validandoErros: {[key:string]: string} = {}
      erros.inner.forEach(erros =>{
        if(!erros.path)return
        validandoErros[erros.path] = erros.message
      })
      formRef.current?.setErrors(validandoErros)
    })
  }

  const handleSubmit = (dados: ICadastroInfo | IInfoClient) => {
    if(type === 'edit'){
      handleEdit(dados)
    }else{
      handleSave(dados)
    }
  }

  const [cepData, setCepData] = useState('')

  function getCEP (cep:string) {
    fetch(`https://viacep.com.br/ws/${cep}/json/`)
      .then(response => response.json())
      .then(data => {
        setCepData(data)
      })
  }

  return (
    <Form
      initialData={client}
      ref={formRef}
      className="Form-Cadastro-Cliente"
      onSubmit={(dados) => {
        dados.id = client?.id
        handleSubmit(dados)
      }}
    >
      <Box
        display={"flex"}
        flexDirection={"column"}
        height={"100%"}
        className="Conteiner-Interior-Geral"
      >
        <Box
          height={70}
          width={"100%"}
          bgcolor={"#575A61"}
          display={"flex"}
          alignItems={"center"}
          paddingLeft={3}
        >
          <Typography
            sx={{ fontWeight: "500", fontSize: "25px", color: "#fff" }}
          >
            Cadastrar Cliente
          </Typography>
        </Box>
        <Box className="Container-Interior-Formulario">
          <Box display={"flex"} justifyContent={"space-around"}>
            <Box
              className="Form-Interior-Top"
              display={"flex"}
              flexDirection={"column"}
            >
              <VTextField label="Nome" name="name" />
              <VSelectField
                sx={{ height: "30px" }}
                name="sex"
                label="Gnero"
                listOption={Gender}
              />
              <VTextField label="RG" name="rg" />
              <VTextField label="CPF" name="cpf" />
            </Box>
            <Box
              className="Form-Interior-Top"
              display={"flex"}
              flexDirection={"column"}
            >
              <VTextField label="Email" name="email" />
              <VTextField label="Nmero Fixo" name="telephone" />
              <VTextField label="Celular" name="cell" />
            </Box>
          </Box>
          <Box flex={1}>
            <Typography sx={{mt:4, ml:2,fontWeight:'500', color: '#575A61'}}>Informaes de Endereo</Typography>
            <Box display={"flex"} justifyContent={"space-around"}>
              <Box className="Form-Interior-Bottom">
                <VTextField label="UF" name="uf" />
                <VTextField label="CEP" name="cep"/>
                <VTextField label="Endereo" name="address" />
                <VTextField label="Cidade" name="city" />
              </Box>
              <Box className="Form-Interior-Bottom">
                <VTextField label="Bairro" name="neighborhood" />
                <VTextField label="Nmero Residncia" name="number" />

                <Box className="Container-Botoes">
                  <Button 
                    sx={{ color: "#fff", width:100, backgroundColor:'#575A61'}}
                    type="submit"
                    variant="contained"
                    onClick={close}
                  >
                    Cancelar
                  </Button>
                  <Button
                    sx={{ color: "#fff", width:100, backgroundColor:'#575A61'}}
                    type="submit"
                    variant="contained"
                  >
                    Salvar
                  </Button>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </Form>
  );
};
